.video-start {
  position: relative;
  overflow: hidden;
  cursor: pointer;

  &:hover .video-start__img {
    transform: scale(1.05);
  }

  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform var(--transition) ease;
  }

  &__overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    display: flex;
    width: 100%;
    //height: auto;
    align-items: flex-end;
    padding: 30px 48px;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 10%, rgba(0, 0, 0, 0.8) 50%);

    @include media('<desktop-sm') {
      padding: 20px 28px;
      line-height: 1.4;
    }

    @include media('<tablet') {
      padding: 15px;
    }
  }

  &__title {
    color: var(--white);
    font-size: 25px;
    font-weight: 600;
    letter-spacing: 0.63px;

    @include media('<desktop-sm') {
      font-size: 18px;
      letter-spacing: 0.74px;
    }

    @include media('<tablet') {
      font-size: 17px;
      letter-spacing: 0.5px;
    }
  }

  &__btn {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 70px;
    height: 70px;
    border: 1px solid var(--white);
    border-radius: 50%;
    cursor: pointer;
    transform: translateX(-50%) translateY(-50%);

    & .icon-play {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 16px;
      height: 19px;
      fill: var(--white);
      transform: translateX(-25%) translateY(-50%);
    }
  }
}
